import React from 'react'

const Toolbar = ({ fontSize, setFontSize, contrast, setContrast }) => {

    return (
        <React.Fragment>
            <div className={`app-toolbar${constrast ? ' app-toolbar--contrast' : ''}`} >
                <div className="app-toolbar--left">
                    <div className="app-toolbar--item" onClick={() => setFontSize(Math.max(fontSize - 10, 50))}>
                        <span>A-</span>
                    </div>
                    <div className="app-toolbar--item" onClick={() => setFontSize(100)}>
                        <span>A</span>
                    </div>
                    <div className="app-toolbar--item" onClick={() => setFontSize(Math.min(fontSize + 10, 150))}>
                        <span>A+</span>
                    </div>
                    <div className="app-toolbar--item" onClick={() => setContrast(!contrast)}>
                        <span>Wersja kontrastowa</span>
                    </div>
                </div>
                <div className="app-toolbar--right">
                    <div className="app-toolbar--item">
                        <span>EN</span>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}

export default Toolbar